"use strict";
import { onUnmounted } from "vue";
export function useWakeLock() {
  if (!("wakeLock" in navigator)) {
    return;
  }
  const wakeLockRequest = navigator.wakeLock.request("screen").catch(() => {
  });
  onUnmounted(async () => {
    const wakeLock = await wakeLockRequest;
    wakeLock?.release();
  });
}
